@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Roboto+Condensed:wght@300&display=swap);
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto Condensed", sans-serif;
}

.condensed {
  font-family: "Roboto Condensed", sans-serif;
}

.mono {
  font-family: "Major Mono Display", monospace;
}

body,
html,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: black;
  color: #ecdbba;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently */
}

iframe {
  border: 0;
}

.App {
  width: 100%;
  height: 100%;
}

.Time {
    text-align: center;
    font-size: 6em;
}

.MediaPlayer {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 70% 30%;
  grid-template-rows: 80% 20%;
  grid-template-areas:
    "screen controls"
    "time time";
}

.MediaPlayerScreen {
  grid-area: screen;
}
.MediaPlayerTime {
  grid-area: time;
}
.MediaPlayerControls {
  grid-area: controls;
}

